import React, {useState} from 'react'
import {useDispatch} from 'react-redux'
import { navigate, Link } from '@reach/router'
import { useForm } from 'react-hook-form'
import axios from 'axios'
import login from '../actions/loginAction'
import './Form.css'
function FormLoginPage() {
    const [authenticating, setAuthenticating] = useState(false)
    const dispatch = useDispatch()
    const [formError, setFormError] = useState({email:'', password:'', company: ''})
    const {handleSubmit, register} = useForm()
    const onLogin = async (value, e) => {
        setAuthenticating(true)
        try {
            const res = await axios.post('/api/login', {
                email: value.email,
                password: value.password
            })
            e.target.reset()
            navigate('/')
            dispatch(login(res.data.user))
        }
        catch(error) {
            console.log(error.response.data.errors)
            setFormError(error.response.data.errors)
        }
        setAuthenticating(false)
    }
    return (
        <form className="auth-form" onSubmit={handleSubmit(onLogin)}>
            <h2>Log In</h2>
            <div className="auth-form-group">
                <label className="form-control" htmlFor="email">Email</label>
                <input className="form-control"  type="text" name="email" autoComplete="off" ref={register}/>
                <p className="form-error">{formError.email}</p>
            </div>
            <div className="auth-form-group">
                <label className="form-control"  htmlFor="password">Password</label>
                <input className="form-control"  type="password" name="password" ref={register}/>
                <p className="form-error">{formError.password}</p>
            </div>
            <button className="form-submit-button" type="submit">Let Me In</button>
            <Link to="/signup"><p className="form-switch">I don't have an account</p></Link>
            {authenticating && <div className="form-authen"><img src="/logo.png" alt="form-loading"/></div>}
        </form>
    )
}

export default FormLoginPage
