import React, {useState} from 'react'
import {useDispatch} from 'react-redux'
import { navigate, Link } from '@reach/router'
import { useForm } from 'react-hook-form'
import axios from 'axios'
import login from '../actions/loginAction'
import './Form.css'
function FormSignupPage() {
    const [authenticating, setAuthenticating] = useState(false)
    const dispatch = useDispatch()
    const [role, setRole] = useState('staff')
    const [formError, setFormError] = useState({})
    const {handleSubmit, register} = useForm()
    const onSignUp = async (value, e) => {
        setAuthenticating(true)
        try {
            const res = await axios.post('/api/signup', {
                email: value.email,
                password: value.password,
                name: value.name,
                founder: role === 'founder',
                companyname: value.companyname,
                companytax: value.companytax,
            })
            setRole('staff')
            e.target.reset()
            navigate('/')
            dispatch(login(res.data.user))
        }
        catch(error) {
            setFormError(error.response.data.errors)
        }
        setAuthenticating(false)
    }
    return (
        <form className="auth-form signup-form" onSubmit={handleSubmit(onSignUp)}>
            <h2>Sign Up</h2>
            <div className="form-content">
                <div className="form-left">
                    <div className="auth-form-group">
                        <label className="form-control" htmlFor="email">Email</label>
                        <input className="form-control"  type="email" name="email" autoComplete="off" ref={register}/>
                        <p className="form-error">{formError.email}</p>
                    </div>
                    <div className="auth-form-group">
                        <label className="form-control" htmlFor="name">Name</label>
                        <input className="form-control"  type="text" name="name" autoComplete="off" ref={register}/>
                        <p className="form-error"></p>
                    </div>
                    <div className="auth-form-group">
                        <label className="form-control"  htmlFor="password">Password</label>
                        <input className="form-control"  type="password" name="password" ref={register}/>
                        <p className="form-error">{formError.password}</p>
                    </div>
                </div>
                <div className="form-right">
                    <div className="auth-form-group">
                        <label className="form-control" htmlFor="role">Role</label>
                        <div className="radio-group">
                            <button type="button" className={role==='staff'? "form-radio radio-active" : "form-radio"}
                            onClick={() => setRole('staff')}>Staff</button>
                            <button type="button" className={role==='founder'? "form-radio radio-active" : "form-radio"}
                            onClick={() => setRole('founder')}>Founder</button>
                        </div>
                        <p className="form-error"></p>
                    </div>
                    {role === 'founder' ?                     
                    <div className="auth-form-group">
                        <label className="form-control"  htmlFor="companyname">Company Name</label>
                        <input className="form-control"  type="text" name="companyname" ref={register}/>
                        <p className="form-error"></p>
                    </div> : ''
                    }

                    <div className="auth-form-group">
                        <label className="form-control"  htmlFor="companytax">Company Tax</label>
                        <input className="form-control"  type="text" name="companytax" ref={register}/>
                        <p className="form-error">{formError.company}</p>
                    </div>
                </div>
            </div>
            <button className="form-submit-button" type="submit">Sign Me Up</button>
            <Link to="/login"><p className="form-switch">I already have an account</p></Link>
            {authenticating && <div className="form-authen"><img src="/logo.png" alt="form-loading"/></div>}
        </form>
    )
}

export default FormSignupPage
