import React, {useState} from 'react'
import { Link, navigate } from '@reach/router'
import { useSelector, useDispatch } from 'react-redux'
import axios from 'axios'
function Header() {
    const dispatch = useDispatch()
    const user = useSelector(state => state.auth)
    const [popup, setPopup] = useState("profile-popup popup-hidden")
    const togglePopup = () => {
        if (popup === "profile-popup popup-hidden")
            setPopup("profile-popup popup-show")
        else
            setPopup("profile-popup popup-hidden")
    }
    const logOut = () => {
        axios.get('/api/logout')
        dispatch({type:'LOGOUT'})
        navigate('/')
    }
    return (
        <div className="header">
            <div className="nav">
                <img src="/logo.png" alt="hexagon" className="header-img"/>
                <Link to="/"><h1 className="title">{user.loggedIn ? user.companyname : 'Moon Light App'}</h1></Link>
            </div>
            {user.loggedIn &&     
            <div className="nav">
                <nav className="nav-bar">
                <Link to='/'><div className="nav-unactive">Home</div></Link>
                <Link to='/signoff'><div className="nav-unactive">Sign-off</div></Link>
                {user.founder && <Link to='/administration'><div className="nav-unactive">Administration</div></Link>}
                <Link to='/dev'><div className="nav-unactive">Dev Log</div></Link>
                </nav>
                <div className="nav-profile" onClick={togglePopup}>
                    <img className="nav-avatar" src={user.avatar} alt="avatar" />
                    <h4 className="nav-username">{user.name}</h4>
                    <div className={popup}>
                        <Link to="/profile"><button className="popup-button">View Profile</button></Link>
                        <button className="popup-button" onClick={logOut}>Log Out</button>
                    </div>
                </div>
            </div>
            }
        </div>
    )
}

export default Header
