import React from 'react';
import { Router } from "@reach/router"
import './App.css';
//Template component
import Template from './components/template/Template'
//Routing component
import PrivateRoute from './components/routes/PrivateRoute'
import AuthRoute from './components/routes/AuthRoute'
//Page component
import FormLoginPage from './components/FormLoginPage'
import FormSignupPage from './components/FormSignupPage'
import HomePage from './components/HomePage'
import SignoffPage from './components/SignoffPage'
import AdminPage from './components/AdminPage'
import ProfilePage from './components/ProfilePage'
import DevLogPage from './components/DevLogPage'

function App() {

  return (
    <div className='App'>
      <Template>
        <Router>
          <HomePage path="/"/>
          <AuthRoute as={FormLoginPage} path="/login"/>
          <AuthRoute as={FormSignupPage} path="/signup"/>
          <PrivateRoute as={SignoffPage} veri={true} admin={false} path="/signoff" />
          <PrivateRoute as={AdminPage} veri={true} admin={true} path="/administration" />
          <PrivateRoute as={ProfilePage} veri={false} admin={false} path="/profile" />
          <PrivateRoute as={DevLogPage} veri={true} admin={false} path="/dev" />
        </Router>
      </Template>
    </div>
  )
}


export default App;
