import React, {useState} from 'react'
import {useSelector, useDispatch} from 'react-redux'
import axios from 'axios'
import './ProfilePage.css'
function ProfilePage() {
    const dispatch = useDispatch()
    const user = useSelector(state => state.auth)
    const [isMouseInside, setIsMouseInside] = useState(false)
    const onMouseEnter = () => setIsMouseInside(true)
    const onMouseLeave = () => setIsMouseInside(false)
    const setProfile = async (e) => {
        let newform = new FormData()
        newform.enctype = 'multipart/form-data'
        newform.append('id', user._id)
        newform.append('avatar', e.target.files[0])
        e.target.value = ''
        const res = await axios.post('/profile', newform)
        dispatch({type:'SET_AVATAR', payload: {avatar: res.data.avatar}})
    }
    return (
        <div className="profile-container">
            <div className="profile-avatar" onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave}>
                <img src={user.avatar} alt="avatar"/>
                <label className={isMouseInside ? "profile-change" : "profile-change profile-change-hidden"}  >
                <input className="profile-avatar-change" type="file" name="avatar" required onChange={setProfile}/>Change Avatar
                </label>
            </div>
            <p className="profile-username">{user.name}</p>
            <p className={user.verified ? "profile-verification verified" : "profile-verification"}>{user.verified ? "Account is verified" : "Account is not verified"}</p>
            <p className={user.verifiedcompany ? "profile-verification verified" : "profile-verification"}>{user.verifiedcompany ? "Company is verified" : "Company is not verified"}</p>
        </div>
    )
}
export default ProfilePage
