const initialState = {
    loggedIn: false,
    email: '',
    name: '',
    founder: false,
    companyname: '',
    companytaxcode: '',
    verified: false,
    avatar: '/logo.png',
    verifiedcompany: false,

}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'LOGIN':
            return {
                ...state,
                loggedIn: true,
                _id: action.payload._id,
                email: action.payload.email,
                avatar: action.payload.avatar + "?" + Date.now(),
                name: action.payload.name,
                founder: action.payload.founder,
                companyname: action.payload.companyname,
                companytaxcode: action.payload.companytaxcode,
                verified: action.payload.verified,
                verifiedcompany: action.payload.verifiedcompany
            }
        case 'LOGOUT':
            return {
                ...state,
                loggedIn: false,
                _id: '',
                email: '',
                avatar: '',
                name: '',
                founder: false,
                companyname: '',
                companytaxcode: '',
                verified: false,
                verifiedcompany: false
            }
        case 'SET_AVATAR':
            return {
                ...state,
                avatar: action.payload.avatar + "?" + Date.now()
            }
        default:
            return state
    }
}
export default authReducer