import React from 'react'
import './HomePage.css'
import { Link, navigate } from '@reach/router'
import { useSelector, useDispatch } from 'react-redux'
import axios from 'axios'
function HomePage() {
    const dispatch = useDispatch()
    const loggedIn = useSelector(state => state.auth.loggedIn)
    const founder = useSelector(state => state.auth.founder)
    const logOut = () => {
        axios.get('/api/logout')
        dispatch({type:'LOGOUT'})
        navigate('/')
    }
    return (
        <div className="home">
            <h1 >Hello! Nice to meet you!</h1>
            {!loggedIn ? 
            <>
            <h2>Log In or Sign Up to continue</h2>
            <nav className="home-nav-container">
                <Link to="/login"><button className="home-nav-button">Log In</button></Link>
                <Link to="/signup"><button className="home-nav-button">Sign Up</button></Link>
            </nav>
            </>
            :
            <>
            <h2>Application List</h2>
            <nav className="home-nav-container">
                <Link to="/signoff"><button className="home-nav-button">Sign-Off</button></Link>
                {founder && <Link to="/administration"><button className="home-nav-button">Administration</button></Link>}
                <Link to="/"><button className="home-nav-button" onClick={logOut}>Log Out</button></Link>
            </nav>
            </>
            }

        </div>
    )
}

export default HomePage
