function login(user) {
    return {
        type: 'LOGIN',
        payload: {
            email: user.email, 
            name: user.name, 
            founder: user.founder, 
            _id: user._id, 
            companyname: user.company.name, 
            companytax: user.company.taxcode,
            avatar: user.avatar,
            verified: user.verified,
            verifiedcompany: user.verifiedcompany
        }
    }
}
export default login