import React from 'react'
import {useSelector} from 'react-redux'
import {Redirect} from '@reach/router'

function PrivateRoute(props) {
    let user = useSelector(state => state.auth)

    let { as: Comp , veri: very, admin: ad, ...prop } = props;
    
    if (!very) {
        return user.loggedIn ? <Comp {...prop} /> : <Redirect to="/" noThrow/>
    } else {
        if (!ad)
            return (user.loggedIn && user.verified && user.verifiedcompany) ? <Comp {...prop} /> : <Redirect to="/" noThrow/>
        else 
            return (user.loggedIn && user.verified && user.verifiedcompany && user.founder) ? <Comp {...prop} /> : <Redirect to="/" noThrow/>
    }
}

export default PrivateRoute