import React, {useState, useEffect} from 'react'
import {useSelector} from 'react-redux'
import axios from 'axios'
import './DevLogPage.css'
import { v4 as uuid } from 'uuid';
function DevLogPage() {
    const email = useSelector(state => state.auth.email)
    const [loading, setLoading] = useState(true)
    const [log, setLog] = useState([])
    const [value, setValue] = useState('')
    const [hoveringLog, setHoveringLog] = useState('')
    const mouseEnter = (id) => {
        setHoveringLog(id)
    }
    const mouseLeave = (id) => {
        setHoveringLog('')
    }
    const deleteLog = async (id) => {
        setLog(log.filter(item => item._id !== id))
        axios.delete('/dev/log', { id })
    }
    const addLog = async (e) => {
        e.preventDefault()
        const idd = uuid()
        setLog([{_id: idd,name: email, log: value},...log])
        setValue('')
        await axios.post('/dev/log', {
            id: idd,
            name: email,
            log: value
        })
    }
    useEffect(() => {
        const getlog = async () => {
            const res = await axios.put('/dev/log', {name: email})
            setLog(res.data.logs.reverse())
            setLoading(false)
        }
        getlog()
    },[email])
    return (
        <div className="dev-container">
            <h1>Dev Log</h1>
            <form onSubmit={addLog}>
                <label htmlFor="log">Question, Log and Everything in Between</label>
                <input type="text" name="log" value={value} onChange={(e) => setValue(e.target.value)}/>
            </form>
            {!loading ? 
            <div className="log-container">
            {log.map(item => 
            <div className="log-item" key={item._id} onMouseOver={() => mouseEnter(item._id)} onMouseLeave={() => mouseLeave(item._id)}>
                <p>{item.log}</p>
                <button className={hoveringLog === item._id ? "log-delete log-visible" : "log-delete"}
                        onClick={() => deleteLog(item._id)}
                >Delete</button>
            </div>
            )}
            </div> :
        <div className="log-container">
            <div className="log-loader">
                <img src="/logo.png" alt="loader"/>
                <h2>Getting Data...</h2>
                <img src="/logo.png" alt="loader"/>
            </div>
        </div>
            }
        </div>
    )
}

export default DevLogPage
